.Emotions{
  font-family: 'Permanent Marker', sans-serif;
  display: flex;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: white;
  user-select: none; /* supported by Chrome and Opera */
   -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
   -moz-user-select: none; /* Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
}
.Emotions__text{
  font-family: 'Permanent Marker', sans-serif;
  flex: 1;
  font-size: 50px;
  color: black;
  text-align: center;
  margin: auto;
}

.menu{
  position: fixed;
  bottom: 0;
  right: 0;
  background: #eee;
  padding: 25px;
  border-radius: 100%;
}
